@tailwind base;
@tailwind components;
@tailwind utilities;

html, body {
  font-family: 'Inter', Arial, sans-serif;
  background: linear-gradient(-45deg, #1e3c72, #2a5298, #9d50bb, #6e48aa);
  background-size: 400% 400%;
  animation: gradientBG 15s ease infinite;
  min-height: 100vh;
}

@keyframes gradientBG {
  0% {background-position: 0% 50%;}
  50% {background-position: 100% 50%;}
  100% {background-position: 0% 50%;}
}

@keyframes gradientText {
  0%, 100% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
}

.animate-gradient {
  background: linear-gradient(90deg, #6366f1, #ec4899, #06b6d4, #6366f1);
  background-size: 200% 200%;
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
  -webkit-text-fill-color: transparent;
  animation: gradientText 4s ease-in-out infinite;
}

.glow-hover {
  position: relative;
  z-index: 1;
  transition: box-shadow 0.3s, transform 0.3s;
}
.glow-hover:hover {
  box-shadow: 0 0 16px 4px #6366f1, 0 0 32px 8px #ec4899;
  transform: scale(1.05);
}

@keyframes skillsBlobGlow {
  0%, 100% {
    filter: drop-shadow(0 0 32px #6366f1) drop-shadow(0 0 16px #06b6d4);
    opacity: 0.7;
  }
  50% {
    filter: drop-shadow(0 0 48px #ec4899) drop-shadow(0 0 32px #06b6d4);
    opacity: 1;
  }
}

.animate-skills-blob-glow {
  animation: skillsBlobGlow 2.5s ease-in-out infinite;
}
